'use client'

import { useState } from 'react'

interface SocialConnection {
  id: string
  name: string
  icon: string
  description: string
  connected: boolean
}

const TABS = [
  { id: 'credit', label: 'Credit' },
  { id: 'social', label: 'Social' },
  { id: 'professional', label: 'Professional' },
  { id: 'gaming', label: 'Gaming' },
  { id: 'on-chain', label: 'On-chain Activities' },
  { id: 'legal', label: 'Legal Identity' },
]

const INITIAL_SOCIAL_CONNECTIONS: SocialConnection[] = [
  {
    id: 'discord',
    name: 'Discord',
    icon: 'üéÆ',
    description: 'Connect your Discord account to identify your reputation.',
    connected: true,
  },
  {
    id: 'google',
    name: 'Google',
    icon: 'G',
    description: 'Connect to Google to verify your email address.',
    connected: false,
  },
  {
    id: 'instagram',
    name: 'Instagram',
    icon: 'üì∏',
    description: 'Connect to Instagram to verify your social media presence.',
    connected: false,
  },
  {
    id: 'x',
    name: 'X',
    icon: 'ùïè',
    description: 'Connect to Twitter to verify your social media presence.',
    connected: false,
  },
]

export function DashboardTabs() {
  const [activeTab, setActiveTab] = useState('social')
  const [connections, setConnections] = useState<SocialConnection[]>(INITIAL_SOCIAL_CONNECTIONS)

  const handleConnection = async (connectionId: string) => {
    // Find the connection
    const connection = connections.find(c => c.id === connectionId)
    if (!connection) return

    try {
      if (connection.connected) {
        // Handle disconnection
        // Here you would typically make an API call to disconnect the service
        setConnections(prev => prev.map(conn => 
          conn.id === connectionId ? { ...conn, connected: false } : conn
        ))
      } else {
        // Handle connection
        // Here you would typically:
        // 1. Open a popup for OAuth
        // 2. Wait for the OAuth flow to complete
        // 3. Make an API call to your backend to verify and store the connection
        
        // For now, we'll just simulate the connection
        setConnections(prev => prev.map(conn => 
          conn.id === connectionId ? { ...conn, connected: true } : conn
        ))
      }
    } catch (error) {
      console.error(`Failed to ${connection.connected ? 'disconnect' : 'connect'} ${connection.name}:`, error)
      alert(`Failed to ${connection.connected ? 'disconnect' : 'connect'} ${connection.name}. Please try again.`)
    }
  }

  return (
    <div className="rounded-2xl bg-[#060709] p-6" >
      <div className="flex gap-2 overflow-x-auto pb-4">
        {TABS.map((tab) => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id)}
            className={`px-4 py-2 rounded-lg whitespace-nowrap transition-colors ${
              activeTab === tab.id
                ? 'bg-white/10 text-white'
                : 'text-gray-400 hover:text-white hover:bg-white/5'
            }`}
          >
            {tab.label}
          </button>
        ))}
      </div>

      <div className="grid gap-4 md:grid-cols-2" >
        {connections.map((connection) => (
          <div id="myClasses" 
            key={connection.id}
            className="p-4 rounded-xl bg-[#1c1d29] flex items-center justify-between"
          >
            <div className="flex items-center gap-4">
              <div className="w-10 h-10 rounded-full bg-[#14151f] flex items-center justify-center text-xl">
                {connection.icon}
              </div>
              <div>
                <h4 className="font-medium">{connection.name}</h4>
                <p className="text-sm text-gray-400">{connection.description}</p>
              </div>
            </div>
            <button
              onClick={() => handleConnection(connection.id)}
              className={`px-4 py-2 rounded-lg text-sm transition-colors ${
                connection.connected
                  ? 'bg-green-500/20 text-green-500 hover:bg-green-500/30'
                  : 'bg-white/5 text-white hover:bg-white/10'
              }`}
            >
              {connection.connected ? 'Connected' : 'Connect'}
            </button>
          </div>
        ))}
      </div>
    </div>
  )
}

